body {
  height: 100vh;
  margin: 0;
}

.stop-watch {
  border: 7px dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.stop-watch__seconds-hand {
  background: linear-gradient(green 50%, #0000 50%);
  border-radius: 7px;
  width: 12px;
  height: 99%;
  animation: 5s linear infinite move-seconds-hand;
  rotate: none;
}

.stop-watch__minute-hand {
  background: linear-gradient(#00f 50%, #0000 50%);
  border-radius: 15px;
  width: 20px;
  height: 50%;
  animation: 60s linear infinite move-minute-hand;
  position: absolute;
  rotate: none;
}

.stop-watch__point-center {
  z-index: 1;
  background-color: #daa520;
  border-radius: 50%;
  width: 5%;
  height: 5%;
  position: absolute;
}

@keyframes move-seconds-hand {
  from {
    rotate: none;
  }

  to {
    rotate: 360deg;
  }
}

@keyframes move-minute-hand {
  from {
    rotate: none;
  }

  to {
    rotate: 360deg;
  }
}

